@import'./../../Common/Styles/SASS/variables';
@import'./../../Common/Styles/SASS/mixins';

.footer {
    background-color: $navBgColor;
    .container {
        @include flex(center,flex-start,wrap);
        flex-direction: column;
        width: 100%;
        .links {
            @include flex(center,space-between,wrap);
            .link {
                height: 40px;
                width: 40px;
                margin: 25px 10px;
                background: $bgColor;
                border-radius: 5px;
                opacity: 0.7;
                &:hover {
                    background: $linksColor;
                    border-radius: 5px;
                    opacity: 1;
                }
            }
        }
        .text {
            max-width: 300px;
            text-align: center;
            color: $bgColor;
            opacity: 0.7;
        }

    }
}






