@import './../../../Common/Styles/SASS/variables';
@import './../../../Common/Styles/SASS/mixins';

.preview {
  margin: 30px 40px;
  width: 450px;
  height: 350px;
  @include flex(center,center,wrap);
  flex-direction: column;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  box-shadow: 10px 20px 40px 0 $shadowColor;

  &:hover::before {
    position: absolute;
    content: '';
    background-color: $navBgColor;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.3;
  }

  @media (max-width: 496px) {
    .preview {
      width: 300px;
      height: 300px;
    }
    .link {
      padding: 2px 10px;
      font-size: 1rem;
      border-radius: 5px;
    }
    .title {
      font-size: 1rem;
    }
  }

  .title {
    color: $bgColor;
    position: relative;
    z-index: 1;
    @include font(1.5rem, center);
  }

  .text {
    color: $bgColor;
    position: relative;
    z-index: 1;
    @include font(1rem, center);
  }

  .linkContainer {
    margin: 15px;
  }

  .link {
    padding: 5px 15px;
    background-color: $titleColor;
    color: $bgColor;
    position: relative;
    z-index: 1;
    @include font(1.2rem, center);
    text-decoration: none;
    border-radius: 5px;

    &:hover {
      background-color:$bgColor ;
      color:$titleColor;
      position: relative;
      z-index: 1;
    }
  }
}











